<template>
  <section class="bg-gray-200 text-primary">
    <div class="container py-20">
      <div class="text-center uppercase font-sans font-bold">
        now people
      </div>
      <div class="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6 my-10">
        <div 
          v-for="(item, i) in data" :key="item.id" 
          class="cursor-pointer" 
          @click="$router.push({name: 'detail-about', params: {id: item.id}})"
          data-aos="fade-up"
          :data-aos-delay="200*i">
          <div class="wrapper">
            <div 
              class="content-img" 
              :style="{'background-image': `url('http://api.badranayalaw.credeva.id/uploads/picture/${item.picture}')`}">
            </div>
          </div>
          <div class="title">
            {{ item.name }}
          </div>
          <div class="font-sans text-sm text-center">             
            {{ item.detail }}
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <!-- <div class="container py-20">
      <Subscribe/>
    </div> -->
  </section>
</template>

<script>
// import Subscribe from '../Subscribe'
import http from '@/config/http'

export default {
  // components: {
  //   Subscribe
  // },
  data: () => ({
    data: [],
  }),
  methods: {
    getData: async function () {
      http.get('team')
        .then(res => {
          this.data = res.data.data
        })
    },
  },
  created() {
    this.getData()
  }
}
</script>

<style scoped>
.wrapper{
  max-height: 500px;
  @apply overflow-hidden;
}
.content-img{
  height: 500px;
  background-size: cover;
  transition-timing-function: ease-in-out;
  transform: scale(1);
  @apply w-full overflow-hidden bg-center;
  @apply duration-300;
}
.content-img:hover{
  transform: scale(1.2);
}
.title{
  @apply text-2xl my-4 text-center capitalize;
}
</style>