<template>
  <section class="bg-gray-200">
    <div class="text-center uppercase font-sans font-bold mb-10 text-primary">
      our clients
    </div>
    <div class="container">
      <!-- <carousel
        :scrollPerPage="false"
        :perPageCustom="[[300, 1], [480, 2], [640, 3], [768, 4], [1024, 5], [1280, 1]]"
        :autoplay="true">
        <slide v-for="item in client" :key="item.id">
          <div>
            <img 
              :src="`http://api.badranayalaw.credeva.id/uploads/picture/${item.logo}`" 
              :title="item.name" 
              alt="client"
              class="object-cover">
          </div>
        </slide>
      </carousel> -->
      <carousel 
        :key="client.length"
        :items="responsive"
        :nav="false"
        :autoplay="false"
        :margin="16"
        :loop="true">
        <div class="flex justify-center" v-for="(item, i) in client" :key="i">
          <img :src="`http://api.badranayalaw.credeva.id/uploads/picture/${item.logo}`">
        </div>
      </carousel>
    </div>
  </section>
</template>

<script>
import { mapState } from "vuex";
// import { Carousel, Slide } from 'vue-carousel';
import carousel from 'vue-owl-carousel2'
export default {
  components: {
    carousel,
    // Carousel,
    // Slide
  },
  data: () => ({
    responsive: 6
  }),
  computed: {
    ...mapState(['client'])
  },
  created() {
    this.$store.dispatch('updateClient')
    const body = document.body.offsetWidth
    if (body > 1240) {
      this.responsive = 6
    } else if (body > 1024) {
      this.responsive = 5
    } else if (body > 768) {
      this.responsive = 3
    } else if (body > 640) {
      this.responsive = 2
    } else {
      this.responsive = 1
    }
  }
}
</script>

<style>
.owl-carousel .owl-stage{
  @apply flex items-center;
}
</style>